.whole-card {
  position: relative;
  width: 132.5px;
  height: 88px;
  top: 0;
  left: 0;
}

.blank-card {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/*Card Colors*/
.positive {
    -webkit-filter: saturate(200%) hue-rotate(95deg);
            filter: saturate(200%) hue-rotate(95deg);
}

.minus {
    -webkit-filter: saturate(200%) hue-rotate(315deg);
            filter: saturate(200%) hue-rotate(315deg);
}

.crit {
    -webkit-filter: saturate(200%) hue-rotate(270deg);
            filter: saturate(200%) hue-rotate(270deg);
}

.standard {

}

/*Card Extras*/
.Plus {
    position: absolute;
    top: 12px;
    left: 40px;
    height: 75%;
}

.Times {
    position: absolute;
    top: 33px;
    left: 50px;
    height: 32%;
}

.One {
    position: absolute;
    top: 20px;
    left: 66px;
    height: 55%;
}

.Two {
    position: absolute;
    top: 20px;
    left: 66px;
    height: 55%;
}

.Back {
    position: absolute;
    top: 2px;
    left: 8px;
    height: 100%;
}



